@media (-webkit-min-device-pixel-ratio: 1.5),(min-device-pixel-ratio: 1.5) {
  border-1px{
    &::after{
      -webkit-transform: scale(0.7);
      transform: scale(0.7);
    }
  }
}
@media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2) {
  border-1px{
    &::after{
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
  }
}
body,html{
  font-weight: 200;
  line-height: 1;
  font-family: "PingFang SC","STHeitiSC-Light","Helvetical-Lingth","arial","sans-serif";
}
.clearfix{
  display: inline-block;
  &:after{
    display:block;
    content:".";
    font-size: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
  }
}



